
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, getYMDHMFormateTime, isArrayType, getImageUrl } from "../../../js/utility.js"
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import event from '../../../js/Event.js'
import { mapState } from 'vuex'
import ExamineOpinionCell from '../components/ExamineOpinionCell.vue'


export default {
name: 'examineview',
components: {
Group,
Cell,
GroupTitle,
Actionsheet,
ExamineOpinionCell
},
computed: {
...mapState([
'detail',
'examineList' 
]),
},
data() {
return {
showRightMenus: false,
rightMenus: {},
listItem: [], //二维数组
add_examine: false, //是否展示操作按钮
type: '', //审批类型
checkList: [],
file_list: [],
examine_detail: {}, //审批详情 用于编辑
permission: false,
}
},
beforeRouteLeave(to, from, next) {
if (to.name === 'examineopinion' || to.name === 'examinehandle') { //审批意见填写页面
this.$store.commit('updateDetail', {
type: 'examineview',
id: this.$route.params.id,
rightMenus: this.rightMenus,
listItem: this.listItem,
add_examine: this.add_examine,
examine_type: this.type,
examine_detail: this.examine_detail,
checkList: this.checkList,
file_list: this.file_list,
permission: this.permission,
refreshList: false, //刷新详情
refreshCheck: false, //刷新审批列表
});
} else {
this.$store.commit('updateDetail', '');
}
next();
},
beforeDestroy() {
event.$off("onClickMore");
},
mounted() {
event.$on('onClickMore', function(path) {
this.showRightMenus = true;
}.bind(this));

if (this.detail !== '' && this.detail.type === 'examineview' && this.detail.id === this.$route.params.id) {
this.rightMenus = this.detail.rightMenus;
this.listItem = this.detail.listItem;
this.add_examine = this.detail.add_examine;
this.type = this.detail.examine_type;
this.checkList = this.detail.checkList;
this.file_list = this.detail.file_list;
if (this.detail.permission) {
this.$store.commit('updateXheaderRightOptions', true);
}
if (this.detail.refreshList) {
this.getDetails();
}
if (this.detail.refreshCheck) {
this.getCheckList();
}
} else {
this.getDetails();
this.getCheckList();
}
},
methods: {
getDetails() {
AjaxHttp('POST', 'm=examine&a=view', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
var permission = res.permission;
if (permission.edit === 1 || permission.delete === 1) {
wself.$store.commit('updateXheaderRightOptions', true);
wself.permission = true;
if (permission.edit === 1) {
wself.rightMenus.edit = '编辑';
}
if (permission.delete === 1) {
wself.rightMenus.delete = '删除';
}
}

if (res.data.file_list) {
wself.file_list = res.data.file_list;
}

if (res.data.add_examine === 1) { //是否有审批权限
wself.add_examine = true;
} else {
wself.add_examine = false;
}
wself.examine_detail = res.data; 
wself.handleDetail(res.data);
}
});
},
getCheckList() {
AjaxHttp('POST', 'm=examine&a=check_list', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success && isArrayType(res.list)) {
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
wself.checkList.push({ name: item.user.full_name, check_time: getYMDHMFormateTime(item.check_time), is_checked: wself.getExamineStatus(item.is_checked), check_color: wself.getCheckColor(item.is_checked), img: getImageUrl(item.user.thumb_path), content: item.content, id: item.check_id });
}
if (res.add_examine === 1) { //是否有审批权限
wself.add_examine = true;
} else {
wself.add_examine = false;
}
}
});
},
handleDetail(data) {
this.type = data.type;
if (data.type === '1') {
var array = [];
array.push({ title: '申请事由', detail: data.content });
this.listItem.push({ title: '', list: array });
} else if (data.type === '2') {
var array = [];
array.push({ title: '请假事项', detail: data.content });
array.push({ title: '请假事由', detail: data.description });
array.push({ title: '请假天数', detail: data.duration });
array.push({ title: '开始时间', detail: getYMDHMFormateTime(data.start_time) });
array.push({ title: '结束时间', detail: getYMDHMFormateTime(data.end_time) });
this.listItem.push({ title: '', list: array });
} else if (data.type === '3') {
var array = [];
array.push({ title: '报销事由', detail: data.content });
array.push({ title: '总金额(元)', detail: data.budget });
this.listItem.push({ title: '', list: array });

if (isArrayType(data.travel)) {
for (var i = 0; i < data.travel.length; i++) {
var listArray = [];
var item = data.travel[i];
listArray.push({ title: '报销项', detail: item.start_address });
listArray.push({ title: '金额(元)', detail: item.money });
listArray.push({ title: '备注', detail: item.description });
this.listItem.push({ title: '事项' + (i + 1), list: listArray });
}
}
} else if (data.type === '4') {
var array = [];
array.push({ title: '报销事由', detail: data.content });
array.push({ title: '总金额(元)', detail: data.budget });
this.listItem.push({ title: '', list: array });

if (isArrayType(data.travel)) {
for (var i = 0; i < data.travel.length; i++) {
var listArray = [];
var item = data.travel[i];
listArray.push({ title: '出发地', detail: item.start_address });
listArray.push({ title: '出发时间', detail: getYMDFormateTime(item.start_time) });
listArray.push({ title: '目的地', detail: item.end_address });
listArray.push({ title: '到达时间', detail: getYMDFormateTime(item.end_time) });
listArray.push({ title: '交通工具', detail: item.vehicle });
listArray.push({ title: '住宿(天)', detail: item.duration });
listArray.push({ title: '金额(元)', detail: item.money });
listArray.push({ title: '备注', detail: item.description });
this.listItem.push({ title: '事项' + (i + 1), list: listArray });
}
}
} else if (data.type === '5') {
var array = [];
array.push({ title: '出差事由', detail: data.content });
array.push({ title: '预算(元)', detail: data.budget });
array.push({ title: '出发地', detail: data.start_address });
array.push({ title: '目的地', detail: data.end_address });
array.push({ title: '出发时间', detail: getYMDHMFormateTime(data.start_time) });
array.push({ title: '返回时间', detail: getYMDHMFormateTime(data.end_time) });
array.push({ title: '交通工具', detail: data.vehicle });
array.push({ title: '住宿(天)', detail: data.duration });
array.push({ title: '备注', detail: data.description });
this.listItem.push({ title: '', list: array });
} else if (data.type === '6') {
var array = [];
array.push({ title: '借款事由', detail: data.content });
array.push({ title: '借款金额(元)', detail: data.budget });
this.listItem.push({ title: '', list: array });
}
},
clickRightMenu(key) {
if (key == 'edit') {
this.$router.push({
name: 'examinehandle',
params: {
id: this.$route.params.id,
type: this.type,
action: 'edit'
}
});
} else if (key == 'delete') {
AjaxHttp('POST', 'm=examine&a=delete', {
'id': this.$route.params.id,
}, this, function(res, wself, success) {
if (success) {
wself.deleteMainList();
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
}
},
examineOpinion(action) {
this.$router.push({
name: 'examineopinion',
params: {
id: this.$route.params.id,
action: action,
type: this.type,
}
});
},
getExamineStatus(status) {
if (status === '1') {
return '通过';
} else if (status === '2') {
return '驳回';
} else {
return '';
}
},
getCheckColor(check) {
if (check === '1') {
return '#5ACD61';
} else if (check === '2') {
return '#FF6600';
} else {
return '#000';
}
},
getUserImage(url) {
return getImageUrl(url);
},
previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
previewerlist.push({
src: getImageUrl(images[i].file_path)
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
deleteMainList() { //转换等操作删除列表数据
var index = -1;
var hanleList = [];

if (this.examineList.examine_type === 1) {
hanleList = this.examineList.me_examine.itemlist;
} else if (this.examineList.examine_type === 2) {
hanleList = this.examineList.examined.itemlist;
} else if (this.examineList.examine_type === 3) {
hanleList = this.examineList.create.itemlist;
}

for (var i = 0; i < hanleList.length; i++) {
var item = hanleList[i];
if (item.examine_id === this.$route.params.id) {
index = i;
}
}

if (index >= 0) {
hanleList.splice(index, 1);
this.$store.commit('updateExamineList', this.examineList);
}
},
}
}




